import { Avatar, Button, Form, Input } from "antd";
import React, { useEffect, useState } from "react";
import useSWR, { mutate } from "swr";
import BreadCrumbs from "../../components/BreadCrumbs/Index";
import Fetcher from "../../utils/Fetcher";
import Error from "../../components/Error/Index";
import axiosInstance from "../../utils/AxiosInstance";
import { toast } from "react-toastify";
import Loader from "../../components/Loader/Index";

const { TextArea } = Input;

export default function Index() {
  const { data, error } = useSWR("/app-config", Fetcher);
  const [app, setApp] = useState({});

  useEffect(() => {
    if (data && data.data) {
      setApp(data.data[0]);
    }
  }, [data]);

  let breadcrumbs = [
    {
      isLink: true,
      link: "/",
      name: "Home",
    },
    {
      isLink: false,
      link: "/settings",
      name: "Settings",
    },
  ];

  const saveConfig = async (values) => {
    try {
      if (app && app._id) {
        await axiosInstance.put(`/app-config/${app._id}`, values);
        toast.success("Config updated successfully!");
        mutate("/app-config", values);
      } else {
        await axiosInstance.post(`/app-config`, values);
        toast.success("Config created successfully!");
        mutate("/app-config", values);
      }
    } catch (error) {
      toast.error(
        error.response?.data.message
          ? error.response?.data.message
          : error.message
      );
    }
  };

  return (
    <>
      <BreadCrumbs details={breadcrumbs} />
      {error ? (
        <Error
          error={
            error.response.data.message
              ? error.response.data.message
              : error.message
          }
        />
      ) : (
        <div className="my-5">
          <div className="w-96 mx-auto">
            <div className="text-center mb-5">
              {app && app._id ? (
                <>
                  <Avatar
                    alt={app?.name}
                    src={app?.logo}
                    className="dark:text-gray-900"
                    size={50}
                  >
                    {app?.name?.split("")[0].toUpperCase()}
                  </Avatar>
                  <Form
                    name="user-details"
                    layout="vertical"
                    initialValues={{
                      name: app?.name,
                      logo: app?.logo,
                      motto: app?.motto,
                      description: app?.description,
                    }}
                    className="ml-5 grid grid-cols-1 gap-2"
                    onFinish={saveConfig}
                  >
                    <Form.Item
                      label={
                        <span className="text-lg dark:text-white">
                          App Name
                        </span>
                      }
                      name="name"
                      rules={[
                        { required: true, message: "Please input app name!" },
                      ]}
                    >
                      <Input allowClear size="large" />
                    </Form.Item>

                    <Form.Item
                      label={
                        <span className="text-lg dark:text-white">Logo</span>
                      }
                      name="logo"
                      rules={[
                        { required: true, message: "Please input logo url!" },
                      ]}
                    >
                      <Input allowClear size="large" />
                    </Form.Item>

                    <Form.Item
                      label={
                        <span className="text-lg dark:text-white">Motto</span>
                      }
                      name="motto"
                    >
                      <TextArea allowClear size="large" />
                    </Form.Item>

                    <Form.Item
                      label={
                        <span className="text-lg dark:text-white">
                          Description
                        </span>
                      }
                      name="description"
                    >
                      <TextArea allowClear size="large" />
                    </Form.Item>

                    <Form.Item>
                      <Button
                        htmlType="submit"
                        size="large"
                        className="w-full"
                        type="primary"
                      >
                        Save
                      </Button>
                    </Form.Item>
                  </Form>
                </>
              ) : (
                <Loader />
              )}
            </div>
          </div>
        </div>
      )}
    </>
  );
}
